/* font-sizes */

.fs-64 {
  font-size: dpr(64px);
}

.fs-48 {
  font-size: dpr(48px);
}

.fs-32 {
  font-size: dpr(32px);
}

.fs-24 {
  font-size: dpr(24px);
}

.fs-16 {
  font-size: dpr(16px);
}

/* alignments */

.left {
  text-align: left;
}

.center {
  text-align: center;
  -ms-flex-item-align: center;
      align-self: center;
}

.right {
  text-align: right;
}

html[dir="rtl"] .left {
  text-align: right;
}

html[dir="rtl"] .right {
  text-align: left;
}

html, body {
  overflow: hidden;
}

#app {
  height: 100%;
  overflow: hidden
}

#app #container {
  height: 100%;
  overflow: hidden;
}

#app #container #progress {/* display: none; */
  position: fixed;
  z-index: 1001;
  top: 0;
  left: 0;
}

#app #container #progress .ant-progress-inner {
  vertical-align: top;
}

#app #container #wrapper {
  height: 100%;
  overflow: hidden;
}

#app #container #wrapper #main {
  overflow: auto;
  position: relative;
}

#app #container #wrapper #main #main-wrapper {
  position: absolute;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
}

