#aside {
  width: 200px;
  /*overflow: hidden;*/
  background: #0C3165 url(/react-spa/static/images/aside-bgi.png) no-repeat bottom
}
#aside #admin-title {
  line-height: 90px;
  font-size: 16px;
  color: #D9DDEC;
  text-align: center;
  background-color: #072454;
}
#aside ul {
  border: none;
  background-color: transparent
}
#aside ul.ant-menu-inline {
  overflow-x: hidden;
}
#aside ul .ant-menu-submenu > .ant-menu-submenu-title {
  color: #7C87AA;
}
#aside ul .ant-menu-submenu.ant-menu-submenu-active > .ant-menu-submenu-title {
  color: #C4CBE6;
}
#aside ul .ant-menu-item > a {
  color: #7C87AA;
}
#aside ul .ant-menu-item.ant-menu-item-active {
  background-color: #11285A
}
#aside ul .ant-menu-item.ant-menu-item-active > a {
  color: #C4CBE6;
}
#aside ul .ant-menu-item.ant-menu-item-selected {
  color: #DAE1FC;
  background-color: #0F2451
}
#aside ul .ant-menu-item.ant-menu-item-selected:after {
  right: auto;
  left: 0;
}
#aside .ant-menu-vertical .ant-menu-sub {
  background-color: #0C3165;
}
#aside #aside-menus {
  position: absolute;
  top: 90px;
  bottom: 0;
  padding-top: 10px
  /*width: 100%;
    overflow-y: auto;
    overflow-x: hidden;
    border: none;*/
}
#aside #aside-menus::-webkit-scrollbar {
  width: 8px;
  height: 8px;
  background-color: rgba(255, 255, 255, .2);
}
#aside #aside-menus::-webkit-scrollbar-thumb {
  border-radius: 6px;
  background-color: rgba(255, 255, 255, .3);
}
#aside #aside-menus::-webkit-scrollbar-button {
  height: 0;
  width: 0;
  background-color: transparent;
}



