#iframe-wrapper {
  width: 100%;
  height: 100%
}
#iframe-wrapper .ant-spin {
  position: absolute;
  z-index: 1;
  width: 50px;
  height: 50px;
  left: 50%;
  top: 50%;
  margin-left: -50px;
  margin-top: -50px;
}
#iframe-wrapper iframe {
  z-index: 11;
}



