#ui-home {
  height: 100%;
  background-color: #F6F6F6
}
#ui-home h2 {
  text-align: center;
  font-size: 28px;
  color: #7C87AA;
}
#ui-home .text {
  padding: 0 6px;
  color: #43cbe6;
  font-size: 42px;
  font-style: italic;
  font-weight: bold;
  font-family: arial,tahoma,"Microsoft YaHei",\5b8b\4f53,sans-serif
}
#ui-home .text .text-a {
  color: #53cfe9;
}
#ui-home .text .text-b {
  color: #73d6ef;
}
#ui-home .text .text-c {
  color: #8edcf3;
}
#ui-home .progress-btn {
  margin: 8px 48px;
  margin: .5rem 3rem;
}



