#header {
  width: 100%;
  height: 50px;
  padding: 0;
  background-color: #2F549A
}

#header .trigger {
  font-size: 18px;
  line-height: 50px;
  padding: 0 16px;
  cursor: pointer;
  color: #EEEEEE;
  vertical-align: top;
  -webkit-transition: color .3s;
  transition: color .3s
}

#header .trigger:hover {
  color: #FFFFFF;
}

#header ul {
  float: right
}

#header ul li {
  float: left
}

#header ul li a {
  display: block;
  line-height: 50px;
  font-size: 14.4px;
  font-size: .9rem;
  font-weight: bold;
  padding: 0 12px;
  color: #EEEEEE
}

#header ul li a:hover {
  color: #FFFFFF;
  background-color: #0C3165;
}

#header ul li.notice i {
  font-size: 20px;
  padding: 1px;
  margin-bottom: 5px;
}

#header ul li.user .avatar {
  vertical-align: middle;
  margin-right: 3px;
}

#header ul li.logout a {
  font-size: 17.6px;
  font-size: 1.1rem;
  padding: 0 18px;
}

/* 弹出层样式 */

.header-user-popover .ant-popover-arrow:after {
  border-bottom-color: #0C3165 !important;
}

.header-user-popover .ant-popover-inner-content {
  padding: 3px 0;
  color: #FFFFFF;
  background-color: #0C3165
}

.header-user-popover .ant-popover-inner-content p {
  padding: 0 10px;
  line-height: 28px;
}

.header-notice-popover .ant-popover-inner-content {
  padding: 0;
  margin-bottom: 0
}

.header-notice-popover .ant-popover-inner-content .ant-tabs-bar {
  border-bottom: 1px solid #e8e8e8;
}

.header-notice-popover .ant-popover-inner-content .ant-tabs-content {
  min-height: 200px;
  line-height: 200px;
  text-align: center;
  font-size: 14px;
}



