#ui-demo {
  padding: 10px
}
#ui-demo .demo-box {
  margin-bottom: 10px;
  background-color: #FFFFFF;
  border: 1px solid #CCCCCC;
  border-radius: 5px
}
#ui-demo .demo-box .demo-title {
  height: 40px;
  line-height: 40px;
  padding: 0 10px;
  font-size: 14px;
  background-color: #9dcdfd;
  border-radius: 6px 6px 0 0;
}
#ui-demo .demo-box .demo-content {
  padding: 10px
}
#ui-demo .demo-box .demo-content .demo-thead {
  height: 40px;
  line-height: 40px;
  background: #364768;
  border-radius: 6px 6px 0 0
}
#ui-demo .demo-box .demo-content .demo-thead::before {
  content: "";
  display: inline-block;
  margin-left: 3%;
  width: 50%;
  height: 10px;
  vertical-align: middle;
  background: #636f84;
}
#ui-demo .demo-box .demo-content .demo-tbody li {
  height: 40px;
  line-height: 40px;
  /* background: #364768; */
  border: 1px solid #e8e8e8;
  border-top: none
}
#ui-demo .demo-box .demo-content .demo-tbody li:last-child {
  border-radius: 0 0 6px 6px;
}
#ui-demo .demo-box .demo-content .demo-tbody li::before {
  content: "";
  display: inline-block;
  margin-left: 3%;
  width: 20px;
  height: 10px;
  background: rgba(79, 169, 251, .6);
}
#ui-demo .demo-box .demo-content .demo-tbody li::after {
  content: "";
  display: inline-block;
  margin-left: 3%;
  width: 50%;
  height: 10px;
  background: rgba(79, 169, 251, .6);
}



